<template>
  <div class="con"> 
    <div class="top">审核弹幕</div>
    <el-card>
      <el-button type="primary" @click="add">审核通过</el-button>
      <el-button type="primary" @click="delect">确定删除</el-button>
      <el-table
        class="spiderTable"
        :data="danmuList"
        border
        stripe
        margin-top="20px"
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column label="学号" prop="number"></el-table-column>
        <el-table-column label="姓名" prop="name"></el-table-column>
        <el-table-column label="弹幕" prop="content"></el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 查询数据
      total:2,
      queryInfo: {
        query: {
          searchName: "",
          searchContent: "",
        },
        pagenum: 1,
        pagesize: 10,
      },
      //   弹幕数据 
      danmuList: [
        // {
        //   id:1,
        //   name: "aaa",
        // },
        // {
        //   id:2,
        //   name: "sss",
        // },
        // {
        //   id:3,
        //   name: "ss",
        // },
        // {
        //   id:4,
        //   name: "shhh",
        // },
      ],
      //   全选情况 通过Id选中爬虫
      checkAll: false,
      checkedSpiders: [],
      isIndeterminate: true,
    };
  },
  created () {
    this.getDanMu()
  },
  methods: {
    async getDanMu () {
      const { data: res } = await this.$http.get('http://bys.nwu.edu.cn:8080/bullet/getUnchecked',{
        params:{
          currentPage:this.queryInfo.pagenum
          }
        })
      // const { data: res } = await this.$http.get('http://10.8.49.234:8082/bullet/addBullet')
      // const { data: res } = await this.$http.get('http://bys.nwu.edu.cn/bullet/getUnchecked?currentPage=',this.queryInfo.pagenum)
      // this.$http.get('http://bys.nwu.edu.cn/bullet/getUnchecked?currentPage=',this.queryInfo.pagenum).then(res=>{
      //   console.log("res", res);
      // }).catch(error => {
      //   throw error;
      // })
      //'taskmanager/student/find/all/students'
      // console.log("res",res)
      if (res.code !== 1200) {
        return this.$message.error('获取弹幕列表失败!')
      }
      this.buttonLoading = true;
      this.danmuList = res.data.bullets
      this.total = res.data.bullets.length
      console.log(res.data.bullets)
    },
    handleSelectionChange(val) {

      // for(var i=0;i<val.length;i++){
      //   this.checkedSpiders.push(val[i].id)
      // }
        // console.log(val[i].id);
        
      this.checkedSpiders=val
      console.log(val);
    },
    //监听Pagesize变化
    handleSizeChange(newSize) {
      console.log(newSize);
      this.queryInfo.pagesize = newSize;
      this.getDanMu();
    },
    //监听页码值变化
    handleCurrentChange(newPage) {
      console.log(newPage);
      this.queryInfo.pagenum = newPage;
      this.getDanMu();
    },
    async delect () {
      var temp ='http://bys.nwu.edu.cn:8080/bullet/notPassBullets?ids='; //一个新的临时数组
      for(var i = 0; i < this.checkedSpiders.length; i++){
            if(i!=0){
              temp+=',';
            }
            temp+=this.checkedSpiders[i].id;
            
        }
    console.log(temp) 
      const { data: res } = await this.$http.get(temp)
      console.log('http://bys.nwu.edu.cn:8080/bullet/notPassBullets?ids=',temp)
      //'taskmanager/student/remove/student/by/id?id=', id
      console.log(res)
      // if (res.result != 'SUCCESS') {
      //   return this.$message.error('删除失败')
      // }
      // this.$message.success('删除成功')
      this.getDanMu()
    },
    async add () {
      var temp ='http://bys.nwu.edu.cn:8080/bullet/checkBullets?ids='; //一个新的临时数组
      for(var i = 0; i < this.checkedSpiders.length; i++){
            if(i!=0){
              temp+=',';
            }
            temp+=this.checkedSpiders[i].id;
            
        }
    console.log(temp) 
      const { data: res } = await this.$http.get(temp)
      console.log('http://bys.nwu.edu.cn:8080/bullet/checkBullets?ids=',temp)
      //'taskmanager/student/remove/student/by/id?id=', id
      console.log(res)
      // if (res.result != 'SUCCESS') {
      //   return this.$message.error('删除失败')
      // }
      // this.$message.success('删除成功')
      this.getDanMu()
    }
  },
};
</script>

<style lang="less" scoped>
.con{
  text-align: center;
}
.el-card{
  margin: 100px;
}
.spiderTable {
  margin-top: 15px;
}
</style>